/*Header*/
.header {
  background: rgb(41, 41, 41);
}

.header-title {
  font-size: 40px;
  padding-top: 20px;
  text-align: center;
  color: rgb(193, 252, 252);
}

.header-nav {
  display: flex;
  width: 20%;
  min-width: 380px;
  justify-content: space-between;
  margin-left: 20px;
  padding-bottom: 20px;
}

.header-link {
  font-size: 30px;
  text-decoration: none;
  color: white;
}
/*Homepage*/
.main-container {
  border: 1px solid rgb(233, 247, 248);
  height: 143vh;
  background: rgb(233, 247, 248);
}

.homepage-title,
.about-title,
.forms-title {
  font-size: 36px;
  text-align: center;
  color: rgb(14, 89, 119);
  padding-top: 20px;
}
/*About us page*/
.about-content {
  padding-left: 20px;
  font-size: 24px;
}
/* Cards */
.cards-container {
  width: 100%;
  height: 88vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.card {
  width: 250px;
  height: 250px;
  margin: 20px;
  display: flex;
  flex-direction: column;
  transition: all 0.4s;
  cursor: pointer;
}

.card:hover {
  transform: scale(1.1);
  transition: all 0.4s;
}

.card-title {
  width: 200px;
  position: relative;
  top: 65px;
  left: 65px;
}

.card-price {
  font-size: 23px;
  position: relative;
  top: -65px;
  left: 31px;
  color: red;
}

.card-color {
  width: 40px;
  font-size: 15px;
  position: relative;
  top: -282px;
  left: 98px;
}

.card-year {
  width: 40px;
  font-size: 20px;
  position: relative;
  top: -180px;
  left: 180px;
}

.card-img {
  box-shadow: 0px 2px 13px grey;
}

/*Search*/
.search {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.search-form {
  margin: auto;
}

.search-text {
  position: absolute;
  top: 335px;
  left: 20px;
  font-size: 20px;
  max-width: 300px;
  color: rgb(14, 89, 119);
}

.search-btn {
  width: 100px;
  font-size: 20px;
  margin-left: 20px;
  border-radius: 15px;
  border: 1px solid rgb(14, 89, 119);
  color: rgb(14, 89, 119);
  transition: all 0.4s;
  cursor: pointer;
}

.search-btn:hover {
  transform: scale(1.1);
  transition: all 0.4s;
}

.search-input {
  height: 23px;
  width: 250px;
  border: 1px solid rgb(14, 89, 119);
}
/*Not found page(404)*/
.not-found-page-title {
  font-size: 40px;
  text-align: center;
}

.link-not-found {
  font-size: 30px;
  margin-left: 100px;
}

.not-found-404 {
  margin: 0;
  font-size: 160px;
  text-align: center;
}
/*Forms page*/
.form {
  margin: auto;
  height: 100%;
  min-height: 455px;
  width: 90%;
  /* max-width: 1050px; */
  min-width: 440px;
  display: flex;
  justify-content: space-around;
  padding: 50px;
  background: rgb(190, 226, 255);
}

.form-column {
  display: flex;
  flex-direction: column;
}

.form-label {
  width: 85%;
  min-width: 280px;
  margin-bottom: 20px;
  padding: 25px;
  border-radius: 5px;
  box-shadow: 2px 2px 13px grey;
}

.title-label {
  margin: 0;
}

.errors {
  color: red;
}

.success-message {
  position: relative;
  font-size: 20px;
  color: green;
}

.mini-card-textfield {
  margin: 5px;
  position: relative;
  top: -60px;
  font-size: 19px;
}

.colors-textfield {
  display: flex;
}

.mini-card-color{
  margin-left: 4px;
  width: 17px;
  height: 17px;
}

.form-btn {
  margin: auto;
  width: 155px;
  height: 40px;
  font-size: 20px;
  background: rgb(177, 247, 243);
  border: none;
  box-shadow: 2px 2px 13px grey;
  border-radius: 10px;
  transition: all 0.4s;
  cursor: pointer;
}

.form-btn:hover {
  transition: all 0.4s;
  transform: scale(1.1);

}

.mini-cards-container {
  width: 100%;
  height: 100vh;
  background: rgb(233, 247, 248);
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.mini-card {
  width: 10%;
  min-width: 200px;
  height: 30%;
  margin: 20px;
  padding: 10px;
  background: rgb(187, 252, 187);
  box-shadow: 2px 2px 10px grey;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mini-card-title {
  position: relative;
  left: -38px;
  top: -95px;
}

.mini-card-image {
  width: 100px;
  height: 100px;
  margin-top: -6px;
  margin-left: 116px;
}

@media (max-width: 1140px) {
  .form {
    flex-direction: column;
  }
}
